@import "src/Constant/colors";
.Dashboard {
  padding-top: 10vw;
  color: $primary_white;
  text-align: start;
  @media (max-width: 768px) {
    padding-top: 20vw;
  }
  .Simplify {
    overflow: hidden;
    height: 35vw;
    padding: 0 8vw 0 8vw;
    position: relative;
    .SimplifyContent {
      width: 50%;
      .SimplifySubContent {
        margin-top: 2.5vw;
        margin-bottom: 3vw;
        @media (max-width: 1024px) {
          padding: 0 3vw;
          text-align: center;
        }
      }
      .SimplifyBtnContainer {
        display: flex;
        align-items: center;
        @media (max-width: 1024px) {
          justify-content: center;
        }
      }
      .Divider {
        padding: 0 0.6vw 0.2vw 0.6vw;
        font-size: 1.6vw;
        opacity: 0.4;
      }
      .SVG {
        width: 1.3vw;
        height: 1.5vw;
        @media (max-width: 1024px) {
          width: 2.3vw;
          height: 2.5vw;
        }
      }
      @media (max-width: 1024px) {
        width: 100%;
      }
    }
    .JoinBtn {
      background: linear-gradient(
        0deg,
        $custom_pink 0,
        $custom_light_pink 45%,
        $custom_light_pink 65%,
        $custom_pink 100%
      );
      background-position: 100% 0;
      background-size: 100% 140%;
      &:hover {
        background-position: 100% 100%;
      }
    }
    @media (max-width: 1024px) {
      height: 85vw;
      flex-direction: column-reverse;
      @media (max-width:426px) {
        height: 155vw;
      }
    }
  }
  .VideoContainer {
    width: 50%;
    position: relative;
    .HeroVideo {
      width: 70vw;
      position: absolute;
      right: -20vw;
      top: -3vw;
      mix-blend-mode: lighten;
      @media (max-width: 1024px) {
        position: initial;
        right: unset;
        top: unset;
        width: 101vw;
        @media (max-width:426px) {
          width: 175vw;
        }
      }
    }
    @media (max-width: 1024px) {
      display: flex;
      justify-content: center;
      width: 100%;
    }
  }
  .Blur2 {
    position: absolute;
    margin-top: -10vw;
    width: 14vw;
  }
}
.Marquee {
  background-color: $custom_light_gray;
  margin-top: 12vw;
  padding: 1vw 0;
  width: 100%;
  white-space: nowrap;
  font-size: 2.2vw;
  .marquee-container {
    height: 100%;
    .Win {
      color: $custom_light_gray;
      margin: 0 3vw 0 0;
      -webkit-text-stroke: 1px $secondary_black;
    }
    .Sign {
      color: $secondary_black;
      font-weight: bold;
      margin: 0 3vw 0 0;
    }
  }
  @media (max-width: 1024px) {
    font-size: 3vw;
    padding: 1.75vw 0;
    @media (max-width:426px) {
      font-size: 4vw;
      padding: 3vw 0;
    }
  }
}
.Features {
  padding-top: 12vw;
  display: flex;
  position: relative;
  .ContentLeft {
    padding: 0 8vw 0 8vw;
    width: 50%;
    .SubContent {
      .FeatureTextTitle {
        @media (max-width: 1024px) {
          justify-content: flex-start;
          @media (max-width:426px) {
            justify-content: center;
          }
        }
      }
      .FeatureTextContent {
        @media (max-width: 1024px) {
          font-size: 1.75vw;
          @media (max-width:426px) {
            text-align: center;
            font-size: 2.75vw;
          }
        }
      }
      @media (max-width: 1024px) {
        width: 60%;
        @media (max-width: 426px) {
          width: 100%;
        }
      }
    }
    .Video {
      position: relative;
      .FeatureVideo {
        width: 68vw;
        mix-blend-mode: lighten;
        margin-left: -18vw;
        margin-top: -5vw;
        @media (max-width: 1024px) {
          margin-left: -15vw;
          margin-top: -8vw;
          @media (max-width: 426px) {
            margin-left: -20vw;
            width: 125vw;
          }
        }
      }
      @media (max-width: 1024px) {
        width: 40%;
        overflow: hidden;
        @media (max-width: 426px) {
          width: 100%;
        }
      }
    }
    @media (max-width: 1024px) {
      display: flex;
      width: 100%;
      @media (max-width:426px) {
        flex-direction: column-reverse;
      }
    }
  }
  .ContentRight {
    width: 50%;
    .TextContainer {
      margin-bottom: 5vw;
      .TextSubContainer {
        padding: 0 4vw;
        .Title {
          font-size: 1.25rem;
          @media (max-width: 1024px) {
            font-size: calc(1.325rem + 0.9vw);
          }
        }
        @media (max-width: 1024px) {
          padding: 0 2vw;
          @media (max-width:426px) {
            text-align: center;
            margin-top: 4vw;
          }
        }
      }
      @media (max-width: 1024px) {
        padding: 0 8vw;
        @media (max-width:426px) {
          flex-direction: column;
        }
      }
    }
    .ImgFeature {
      width: 8.5vw;
      height: 8.5vw;
      margin-right: 2.5vw;
      @media (max-width: 1024px) {
        width: 15.5vw;
        height: 15.5vw;
        margin-right: 5.5vw;
        @media (max-width:426px) {
          width: 27.5vw;
          height: 27.5vw;
          margin-right: 0;
        }
      }
    }
    .Blur1 {
      position: absolute;
      top: -8vw;
      right: 0;
      width: 14vw;
    }
    .Fow1 {
      position: absolute;
      top: -6vw;
      right: 0;
      @media (max-width: 768px) {
        height: 265vw;
      }
    }
    @media (max-width: 1024px) {
      width: 100%;
      @media (max-width:426px) {
        margin-top: 20vw;
      }
    }
  }
  @media (max-width: 1024px) {
    display: block;
  }
}
.SignUp {
  margin-top: 18vw;
  padding: 0 0 0 8vw;
  position: relative;
  .SubTitle {
    margin-top: 2vw;
    font-weight: bold;
    font-size: 1.75vw;
    @media (max-width: 1024px) {
      font-size: 3vw;
      @media (max-width:426px) {
        font-size: 6.25vw;
      }
    }
  }
  .TrickleTitle {
    justify-content: flex-start;
  }
  .SignInputGroup {
    width: 40%;
    height: 100%;
    position: relative;
    .SignInput {
      background-color: transparent;
      border-radius: 1rem;
      border-top-right-radius: 1rem !important;
      border-bottom-right-radius: 1rem !important;
      box-shadow: unset;
      color: white;
      font-size: 1vw;
      padding: 1.25vw;
      padding-right: 8rem;
      &:hover {
        &::placeholder {
          color: lightgray;
        }
      }
      &:focus {
        border-color: #ada3d2;
      }
      &::placeholder {
        color: white;
      }
      @media (max-width:426px) {
        font-size: 3.5vw;
        padding: 3.25vw;
        padding-right: 5rem;
      }
    }
    .SignBtn {
      position: absolute;
      top: 0.05vw;
      right: 0.0125vw;
      z-index: 3;
      align-items: center;
      color: white;
      background: linear-gradient(
        0deg,
        $custom_light_blue 0,
        $custom_blue 45%,
        $custom_blue 65%,
        $custom_light_blue 100%
      );
      background-position: 100% 0;
      background-size: 100% 140%;
      border-width: 0;
      border-radius: 1rem;
      border-top-left-radius: 1rem !important;
      border-bottom-left-radius: 1rem !important;
      display: flex;
      font-size: 1.2vw;
      height: 94%;
      padding: 2vw;
      width: 25%;
      justify-content: center;
      transition: 0.3s all ease;
      &:hover {
        background-position: 100% 100%;
      }
      &:focus {
        box-shadow: unset;
      }
    }
    @media (max-width:426px) {
      width: 75%;
    }
  }
  .Access {
    width: 44vw;
    position: absolute;
    right: 0;
    top: -10vw;
    opacity: 0.7;
  }
  .Blur3 {
    position: absolute;
    top: 8.5vw;
    left: 33.5vw;
  }
  .Fow2 {
    position: absolute;
    right: 0;
    top: 0;
    width: 43vw;
  }
}
.Refer {
  margin-top: 15vw;
  position: relative;
  .Refer1 {
    mix-blend-mode: lighten;
    position: absolute;
    width: 12vw;
    left: -5.5vw;
    top: 1vw;
  }
  .Refer2 {
    mix-blend-mode: lighten;
    position: absolute;
    left: 2vw;
    top: 10vw;
    width: 17vw;
  }
  .Refer3 {
    mix-blend-mode: lighten;
    position: absolute;
    right: -8vw;
    top: 0;
    width: 29vw;
  }
}
.Footer {
  margin-top: 15vw;
  padding: 0 8vw 0 8vw;
  .FooterContainer {
    display: flex;
    justify-content: space-between;
    border: 0.005rem solid $custom_borderline;
    border-left: 0;
    border-right: 0;
    padding-top: 3vw;
    padding-bottom: 3vw;
    .FooterLogo {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      .LogoImg {
        width: 12vw;
        @media (max-width:426px) {
          width: 40vw;
        }
      }
      .LogoContent {
        color: $custom_footer_text;
        font-size: 1.15vw;
        @media (max-width:426px) {
          text-align: start;
          font-size: 3vw;
        }
      }
    }
    .FooterLink {
      color: white;
      .FooterSubContainer {
        margin-left: 6.5vw;
      }
      .LinkTitle {
        font-weight: bold;
        font-size: 1.15vw;
        margin-bottom: 1vw;
        @media (max-width:426px) {
          font-size: 3vw;
        }
      }
      .LinkContent {
        z-index: 1;
        font-size: 1vw;
        color: $custom_footer_text;
        padding-left: 0;
        @media (max-width:426px) {
          font-size: 3vw;
        }
      }
      @media (max-width:1024px) {
        justify-content: center;
        margin-top: 7.5vw;
      }
    }
    @media (max-width:426px) {
      flex-direction: column;
      justify-content: flex-start;
    }
  }
  .FooterCopyRight {
    padding: 2vw;
    color: $custom_footer_text;
    p {
      margin-bottom: 0;
      font-size: 0.75vw;
      text-align: center;
      @media (max-width:426px) {
        font-size: 3vw;
        margin-bottom: 15vw;
      }
    }
  }
}
.TextTitle {
  font-size: 4vw;
  line-height: 4.5vw;
  .Important {
    font-weight: bold;
    color: $custom_light_blue;
  }
  @media (max-width: 1024px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    font-size: 5vw;
    line-height: 7.5vw;
    @media (max-width:426px) {
      font-size: 9vw;
      line-height: 12.5vw;
    }
  }
}
.TextSubTitle {
  font-size: 2vw;
}
.TextContent {
  color: $custom_light_black;
  font-size: 1.3vw;
  @media (max-width: 1024px) {
    font-size: 2vw;
    @media (max-width:426px) {
      font-size: 3.25vw;
    }
  }
}
.PublicBtn {
  align-items: center;
  background: linear-gradient(0deg, $custom_light_blue 0, $custom_blue 45%, $custom_blue 65%, $custom_light_blue 100%);
  background-position: 100% 0;
  background-size: 100% 140%;
  border-width: 0;
  border-radius: 1.5rem;
  display: flex;
  font-size: 1.2vw;
  padding: 1vw 2.5vw;
  transition: 0.3s all ease;
  &:hover {
    background-position: 100% 100%;
  }
  &:focus {
    box-shadow: unset;
  }
  @media (max-width: 1024px) {
    font-size: 2.2vw;
    padding: 3vw 6vw;
    @media (max-width:426px) {
      font-size: 4vw;
      padding: 5vw 6vw;
    }
  }
}
