@import "src/Constant/colors";
.App {
  text-align: center;
  min-height: 100vh;
  max-width: 100vw;
  background-color: $secondary_black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: clash-display-bold;
  src: url(./assets/fonts/ClashDisplayBold/ClashDisplay-Bold.woff2) format("woff2"),
    url(./assets/fonts/ClashDisplayBold/ClashDisplay-Bold.woff) format("woff");
}
@font-face {
  font-family: clash-display-extralight;
  src: url(./assets/fonts/ClashDisplayExtralight/ClashDisplay-Extralight.woff2) format("woff2"),
    url(./assets/fonts/ClashDisplayExtralight/ClashDisplay-Extralight.woff) format("woff");
}
@font-face {
  font-family: clash-display-semibold;
  src: url(./assets/fonts/ClashDisplaySemibold/ClashDisplay-Semibold.woff2) format("woff2"),
    url(./assets/fonts/ClashDisplaySemibold/ClashDisplay-Semibold.woff) format("woff");
}
@font-face {
  font-family: clash-display-regular;
  src: url(./assets/fonts/ClashDisplayRegular/ClashDisplay-Regular.woff2) format("woff2"),
    url(./assets/fonts/ClashDisplayRegular/ClashDisplay-Regular.woff) format("woff");
}
